.video-content__text-wrapper {
  margin: 0 20px 38px 20px;
  text-align: center;
}

.video-content__title {
  .important-text;
  margin: 0;
  margin-bottom: 35px;
}

.video-content__text {
  .text;
  margin: 0;
  text-align: center;
}

.video-content__link {
  margin: 0 20px;
  display: block;
}

@media (min-width: @tablet-width) {
  .video-content__text-wrapper {
    margin: 0 60px 38px 60px;
  }

  .video-content__link {
    margin: 0 60px;
  }
}

@media (min-width: @desktop-width) {
  .video-content__text-wrapper {
    margin: 0 120px 38px 120px;
  }

  .video-content__link {
    margin: 0 120px;
  }
}
